.team {
 padding: 60px 0;
 &-text {
  font-size: 38px;
  color: #ffffff;
  font-weight: normal;
  margin: 0px 0 25px 0;
  text-align: center;
  text-transform: capitalize; }
 &-descr {
  font-size: 16px;
  color: rgba(255, 255, 255, 0.71);
  font-weight: 400;
  text-align: center;
  margin: 0 0 60px 0;
  line-height: 28px;
  margin-bottom: 40px; } }
.test {
 &__wrapp {
  display: flex;
  justify-content: center;
  flex-wrap: wrap; }
 &-block {
  flex-basis: 20%;
  &__img {
   float: left;
   width: 60px;
   height: auto;
   border-radius: 50%; }
  &__text {
   float: left;
   width: 70%;
   text-align: left;
   margin-left: 10px; }
  &__title {
   line-height: 1.1;
   color: #888888;
   font-size: 18px;
   margin-top: 10px;
   margin-bottom: 10px; }
  &__descr {
   line-height: 24px;
   margin: 0;
   color:  grey; } } }
@media (max-width: 1024px) {
 .test {
  &__wrapp {}
  &-block {
   flex-basis: 25%; } } }
@media (max-width: 768px) {
 .test {
  &__wrapp {
   align-items: center;
   flex-direction: column; }
  &-block {
   width: 60%;
   margin-bottom: 5%;
   display: flex;
   flex-direction: column;
   align-items: center;
   &__title {
    text-align: center; }
   &__descr {
    text-align: center; } } } }



