.menu {
 &__list {
  display: flex;
  list-style: none;
  justify-content: center;
  flex-wrap: wrap;
  border-bottom: 1px solid #383838;
  padding: 10px 0px; }
 &__item {
  font-size: 14px;
  margin-left: 25px;
  margin-right: 25px; }
 &__link {
  color: #7b959c;
  &:hover {
   color: #00a6f1;
   background-color: transparent; } } }
.content {
	margin-bottom: 5%;
	&__info {
	 display: flex;
	 flex-direction: column;
	 flex-wrap: wrap;
	 align-items: center; }
	&__title {
	 font-size: 36px;
  color: #BEB7B7;
  font-weight: normal;
  line-height: 1.1;
  margin: 80px 0 5px 0;
  text-align: center; }
 &__text {
  font-size: 16px;
  color: rgba(255, 255, 255, 0.71);
  text-align: center;
  margin: 0 0 60px 0;
  line-height: 28px; }
 &-blocks {
  display: flex;
  margin-left: 15%;
  margin-right: 15%;
  &__text {
   width: 50%;
   position: relative;
   padding-right: 15px;
   padding-left: 15px; }
  &__stat {
   width: 50%; }

  .text {
   &__about {
    padding: 0;
    margin-left: 3%;
    &-list {
     color: #888888;
     font-size: 0.9375em;
     line-height: 1.625em;
     margin-bottom: 10px;
     &:before {
      content: "\f058";
      position: absolute;
      font-family: FontAwesome;
      font-size: 1.125em;
      color: #03a9f4;
      left: 10px; } } }
   &__header {
    font-size: 22px;
    color: #afafaf;
    margin: 0 0 5px 0; }
   &__main {
    color: #888888;
    font-size: 0.9375em;
    line-height: 1.625em;
    padding: 15px 0; }
   &__stat {
    color: #888;
    font-size: .9375em;
    line-height: 1.625em;
    padding-top: 38px; }
   &__wrap {
    margin-top: 8%; } } } }
.menu__icon {
 display: none;
 width: 35px;
 height: 35px;
 position: relative;
 cursor: pointer;
 &-span {
  display: block;
  position: absolute;
  height: 7px;
  width: 100%;
  background: #333333;
  border-radius: 9px;
  opacity: 1;
  left: 0;
  transform: rotate(0deg);
  transition: .25s ease-in-out;
  &:nth-child(1) {
   top: 1px; }
  &:nth-child(2) {
   top: 11px; }
  &:nth-child(3) {
   top: 11px; }
  &:nth-child(4) {
   top: 20px; } } }
.menu.menu_state_open .menu__icon span:nth-child(1) {
 top: 18px;
 width: 0%;
 left: 50%; }
.menu.menu_state_open .menu__icon span:nth-child(2) {
 transform: rotate(45deg); }
.menu.menu_state_open .menu__icon span:nth-child(3) {
 transform: rotate(-45deg); }
.menu.menu_state_open .menu__icon span:nth-child(4) {
 top: 18px;
 width: 0%;
 left: 50%; }
.menu.menu_state_open .menu__links {
 display: block; }
//statistic
.container {
 width: 100%;
 background-color: #ddd;
 &__text {
  margin: 10px; } }
.skills {
 text-align: right;
 padding-right: 20px;
 line-height: 20px;
 color: white; }
.html {
 width: 90%;
 background-color: #4CAF50; }
.css {
 width: 80%;
 background-color: #2196F3; }
.js {
 width: 65%;
 background-color: #f44336; }
.php {
 width: 60%;
 background-color: #808080; }
@media (min-width: 1024px) {
 .wrap {
  max-width: 1024px; } }
@media (max-width: 1024px) {
 .content {
  &-blocks {
   margin-left: 3%;
   margin-right: 3%; } } }
@media (max-width: 980px) {
 .menu__list {
  flex-wrap: wrap; }
 .menu__item {} }
@media (max-width: 850px) {
 .menu {
  &__list {
   display: block;
   text-align: center;
   width: 100%; }
  &__link {
   display: block;
   padding: 10px 11px;
   font-size: 12px; } }
 .content {
  &__title {
   font-size: 30px; }
  &__text {
   font-size: 16px; }
  &-blocks {
   display: block;
   &__text {
    width: 100%; }
   &__stat {
    width: 100%; } } } }
.content-blocks .text__header {
 font-size: 18px; }
@media (max-width: 580px) {
 .menu {
  &-icon {
   display: block;
   height: 60px;
   border-bottom: 1px solid #383838; }
  &__icon {
   display: block;
   float: right;
   margin: 10px 10px;
   &-span {
    background: #0797d8; } }
  &__list {
   position: fixed;
   display: none;
   top: 0;
   right: 0;
   left: 0;
   margin-top: 52px;
   background-color: rgba(0, 0, 0, 0.8);
   z-index: 1000;
   overflow: auto; }
  &__link {
   display: block;
   padding: 10px 0;
   text-align: center;
   color: #ffffff; } }
 .content__title {
  margin: 0;
  margin-bottom: 25px;
  font-size: 26px; }
 .content__info {
  margin-top: 20%; }
 .content__text {
  font-size: 14px; }
 .menu_state_open .menu__list {
  display: flex;
  flex-direction: column;
  position: relative;
  background: #232323; } }









