.contacts {
 background: #03a9f4;
 &__wrapp {
  padding: 90px 0 30px;
  color: #fff; }
 &-text {
  font-size: 38px;
  color: #ffffff;
  font-weight: normal;
  margin: 0px 0 25px 0;
  text-align: center;
  text-transform: capitalize; }
 &-descr {
  font-size: 16px;
  color: rgba(255, 255, 255, 0.71);
  font-weight: 400;
  text-align: center;
  margin: 0 0 60px 0;
  line-height: 28px;
  margin-bottom: 40px; } }
.form {
 display: flex;
 justify-content: center;
 &-text {
  margin-right: 2%; }
 &__pole {
  width: 100%;
  height: 34px;
  background: #009fe6;
  padding: 20px 15px;
  border-color: #9adfff;
  border-radius: 0;
  color: #fff;
  font-size: 14px;
  line-height: 1.42857143;
  border: 1px solid #ccc; } }
input {
 &::placeholder {
  color: #fff; } }
.textar {
 width: 100%;
 background: #009fe6;
 padding: 20px 15px;
 border-radius: 0;
 color: #fff;
 font-size: 14px;
 line-height: 1.42857143;
 border: 1px solid #ccc; }
textarea {
 &::placeholder {
  color: #fff; } }
.btn {
 color: #03a9f4;
 background-color: #ffffff;
 border-color: #ffffff;
 padding: 10px 34px;
 border-radius: 0;
 float: right;
 z-index: 1;
 position: relative;
 outline: none;
 overflow: hidden;
 transition: color 0.4s ease-in-out;
 &:before {
  content: '';
  z-index: -1;
  position: absolute;
  top: 100%;
  right: 100%;
  width: 1em;
  height: 1em;
  border-radius: 50%;
  background-color: #009fe6;
  transform-origin: center;
  transform: translate3d(50%, -50%, 0) scale3d(0, 0, 0);
  transition: transform 0.45s ease-in-out; }
 &:hover {
  cursor: pointer;
  color: #161616; }
 &:hover::before {
  transform: translate3d(50%, -50%, 0) scale3d(15, 15, 15); } }
.footer {
  border-top: 1px solid #31bfff;
  margin-top: 3%;
  text-align: center;
  padding-top: 20px;
  &-link {
   color: #232323; } }
@media (max-width: 1024px) {
 .form-main {
  width: 60%; } }
@media (max-width: 580px) {
 .form {
  flex-direction: column;
  align-items: center;
  &-main {
   width: 80%; }
  &-text {
   &__links {
    margin-bottom: 10%; } } } }


