.service {
	padding-top: 95px;
	padding-bottom: 55px;
	background: #03A9F4;
	&__wrap {
	 padding-right: 15px;
	 padding-left: 15px;
	 margin-right: auto;
	 margin-left: auto; }
	&__text {
	 display: flex;
	 justify-content: center;
	 color: #fff;
	 font-size: 26px;
	 line-height: 1;
	 margin: 0;
	 margin-bottom: 30px; }
	&__descr {
	 display: flex;
	 justify-content: center;
	 color: #fff;
	 font-size: 14px;
	 margin: 0 0 60px 0; }
	&-blocks {
	 display: flex;
	 justify-content: center;
	 &__row {
	  flex-basis: 17%;
	  margin-left: 5%;
	  &:nth-child(1) {
    margin-left: 0; } }
	 &__wrap {
	  padding: 40px 0 40px; }
	 &__icon {
	  text-align: center;
	  width: 50%;
	  margin-left: 25%;
	  height: 70px;
	  border-bottom: 1px solid #fff; }
	 &__main {
  	display: flex;
	  justify-content: center;
	  color: #fff;
	  font-size: 16px; }
	 &__parag {
	  text-align: center;
	  color: #fff;
	  line-height: 24px;
	  font-size: 14px; } } }
.fa {
	color: #fff; }
@media (max-width: 850px) {
	.service-blocks {
	 flex-direction: column;
	 align-items: center;
	 &__row {
	  margin-bottom: 35px;
	  width: 50%;
	  margin-left: 0; } } }




