.portfolio {
 padding-top: 110px;
 padding-bottom: 455px;
 background: #03a9f4;
 &-title {
  &__text {
   color: #ffffff;
   font-size: 34px;
   letter-spacing: -1px;
   margin: 0 0 15px;
   text-align: center; }
  &__descr {
   font-size: 16px;
   color: rgba(255, 255, 255, 0.71);
   text-align: center;
   margin: 0 0 60px 0;
   line-height: 28px; } }
 &__menu {
  margin-top: 10px;
  margin-bottom: 40px;
  text-align: center;
  display: block;
  float: none;
  z-index: 2;
  position: relative;
  &-list {
   margin-top: 0;
   margin-bottom: 10px; } } }
.menu-item {
	display: inline-block;
	margin: 0 5px;
	&__link {
	 color: #337ab7; }
	&__text {
	 border-radius: 0;
  display: inline-block;
  font-size: 12px;
  line-height: 20px;
  margin-bottom: 5px;
  padding: 10px 22px;
  text-transform: uppercase;
  transition: all 0.3s ease-in-out 0s;
  background: transparent;
  border: #2b2b2b solid 1px;
  color: #2b2b2b;
  &:hover {
   color: #03a9f4;
   background: #2b2b2b; } } }
.no-touch {
  margin: 0;
  padding: 0;
  background: #EEE;
  font: 10px/13px 'Lucida Sans',sans-serif; }

.wrap {
  margin: auto;
  width: 100%;
  max-width: 900px; }

.box {
  float: left;
  position: relative;
  width: 25%;
  padding-bottom: 20%; }

.boxInner {
  position: absolute;
  left: 10px;
  right: 10px;
  top: 10px;
  bottom: 10px;
  overflow: hidden;
  img {
    width: 100%;
    height: 180px; }
  .titleBox {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    margin-bottom: -50px;
    background: #000;
    background: rgba(0, 0, 0, 0.5);
    color: #FFF;
    padding: 10px;
    text-align: center;
    transition: all 0.3s ease-out; } }

.no-touch {
  &.no-touch .boxInner:hover .titleBox, &.touch .boxInner.touchFocus .titleBox {
    margin-bottom: 0; } }

@media only screen and (max-width: 480px) {
  .box {
    width: 100%;
    padding-bottom: 100%; }
  .portfolio {
    padding-bottom: 2560px; }
  .boxInner {
   img {
    height: 300px; } }
  .portfolio__menu-list {
    padding: 0; }
  .menu-item__text {
   padding: 10px 12px; } }


@media only screen and (max-width: 650px) and (min-width: 481px) {
  .box {
    width: 50%;
    padding-bottom: 50%; }
  .portfolio {
    padding-bottom: 1225px; }
  .boxInner {
   img {
    height: 300px; } } }

@media only screen and (max-width: 1050px) and (min-width: 651px) {
  .box {
    width: 33.3%;
    padding-bottom: 33.3%; }
  .portfolio {
    padding-bottom: 955px; }
  .boxInner {
   img {
    height: 300px; } } }

@media only screen and (max-width: 1290px) and (min-width: 1051px) {
  .box {
    width: 25%;
    padding-bottom: 25%; }
  .portfolio {
    .portfolio {
      padding-bottom: 955px; }
    .boxInner {
     img {
      height: 300px; } } } }

