.experience {
 padding: 0 50px;
 color: #888;
 &__text {
  text-align: center;
  margin-top: 5%;
  font-size: 32px;
  color: #beb7b7; } }
.message {
	display: flex;
	justify-content: center;
	&__wrap {
	 margin-bottom: 8%;
	 margin-top: 8%; }
 &-item {
  flex-basis: 25%;
  margin-left: 5%;
  padding: 10px;
  background: #161717;
  &:nth-child(1) {
   margin-left: 0; }
  &__icon {
   text-align: center;
   width: 50%;
   margin-left: 25%;
   height: 70px; }
  &__main {
   font-size: 18px;
   text-align: center;
   color: #afafaf; }
  &__parag {
   text-align: center;
	  line-height: 24px;
	  font-size: 14px; } } }
@media (max-width: 1024px) {
 .message {
  &-item {
   flex-basis: 30%; } }
 .experience__text {
  font-size: 32px; } }
@media (max-width: 768px) {
 .message {
  flex-direction: column;
  align-items: center;
  &-item {
   margin-left: 0;
   width: 70%;
   margin-bottom: 5%; } } }
@media (max-width: 580px) {
 .message {
  &-item {
   width: 110%; } }
 .experience__text {
  font-size: 24px; }
 .message-item__parag {
  font-size: 12px; } }





