.main {
 &__page {
  position: relative;
  font-family: {}
  padding: 0;
  overflow: hidden;
  text-align: center;
  background-color: #03A9F4;
  margin: 0; }
 &__btn {
  text-decoration: none;
  display: block;
  margin: 0 auto;
  width: 42px;
  height: 21px;
  cursor: pointer;
  padding-bottom: 4%;
  margin-bottom: 40px;
  animation-name: animateArrow;
  animation-duration: 1s;
  animation-iteration-count: infinite; }
 &-header {
  height: 210px;
  background: #0797d8; }
 &-avatar {
  position: relative;
  top: -80px;
  width: 150px;
  height: 150px;
  max-width: 150px;
  max-height: 150px;
  right: 0px;
  left: 0px;
  margin: 0 auto;
  overflow: hidden;
  border-radius: 50%;
  border: 5px solid rgba(255,255,255,0.5);
  &__img {
   width: 165px;
   height: auto;
   max-width: 165px;
   margin-left: -11px; } }
 &-info {
  position: relative;
  padding: 0 8px 22px;
  top: -50px;
  &__title {
   margin-bottom: 10px;
   font-size: 26px;
   line-height: 1;
   color: #ffffff;
   vertical-align: middle;
   &-link {
    color: #ffffff;
    cursor: pointer; } }
  &__descr {
   overflow: hidden;
   font-size: 14px;
   line-height: 22px;
   color: rgba(255, 255, 255, 0.77);
   text-overflow: ellipsis;
   &-p {
    line-height: 24px;
    margin: 0; } }
  &__descr b {
   margin: 0;
   color: #00608b; } }
 &-bottom {
  padding: 0 20px;
  margin-bottom: 50px;
  &__social {
   display: block;
   width: 46px;
   height: 46px;
   text-align: center;
   line-height: 46px;
   font-size: 18px;
   color: #00283a; }
  &__links {
   padding: 0;
   display: block;
   overflow: hidden;
   list-style: none;
   text-align: center;
   margin: 0 auto;
   &-link {
    display: inline-block;
    margin-right: 4px;
    border: 1px solid #285d6b;
    border-radius: 50%;
    padding: 0px;
    &:hover {
     color: #55acee; } } } } }
.social_links {
 padding: 0;
 margin: 0;
 display: block;
 overflow: hidden;
 list-style: none;
 margin: 0 auto;
 text-align: center; }
.social_links li {
 display: inline-block;
 margin-right: 4px;
 border: 1px solid #285d6b;
 border-radius: 50%;
 padding: 0px; }
.social_links li a {
 display: block;
 width: 46px;
 height: 46px;
 text-align: center;
 line-height: 46px;
 font-size: 18px;
 color: #00283a; }
.social_links li a:hover, .social_links li a:focus {
 text-decoration: none;
 border-radius: 80px;
 background: #163840;
 color: #fff; }
.twitter a:hover {
 color: #55acee; }
.facebook a:hover {
 color: #3b5998; }
.pinterest a:hover {
 color: #cb2026; }
.gplus a:hover {
 color: #dd4b39; }

@keyframes animateArrow {
 0% {
  transform: translateY(0px); }
 50% {
  transform: translateY(25px); }
 100% {
  transform: translateY(0px); } }
@media (max-width: 1024px) {
 .main-info {
  padding: 0 8px 0; }
 .main {
  &-header {
   height: 180px; }
  &__btn {
   padding-bottom: 50px; } } }
@media (max-width: 768px) {
 .main__btn {
  padding-bottom: 60px; }
 .main {
  &-header {
   height: 160px; } } }

@media (max-width: 580px) {
 .main__btn {
  padding-bottom: 40px; }
 .main {
  &-bottom {
   display: none; }
  &-header {
   height: 140px; }
  &-info {
   padding: 0 10px 0; } } }





